<template>
  <section class="dashboard-content">
    <div class="form-section">
      <div class="section-title">更新订单</div>
      <div class="mt-m">
        <span class="label" type="text">导入订单</span>
        <el-button size="small" @click="$refs.upload.click()">选择文件</el-button>
        <span class="remark" v-if="file">{{ file.name }}</span>
      </div>
      <div class="mt-m">
        <el-button size="small" type="primary" @click="uploadFile" :disabled="!file" :loading="!isImported">确认导入</el-button>
        <span class="remark">新上传的订单如果以前上传过则以新上传的为准</span>
      </div>
      <input type="file" ref="upload" @change="chooseFile">
    </div>
    <div class="form-section">
      <div class="section-title mb-m">筛选订单</div>
      <label class="filter-label">
        <span class="label-content">订单ID</span>
        <el-input v-model="filter.id"></el-input>
      </label>
      <label class="filter-label">
        <span class="label-content">下单时间</span>
        <el-date-picker
          type="datetimerange"
          format="yyyy-MM-dd HH:mm:ss"
          value-format="yyyy-MM-dd HH:mm:ss"
          v-model="filter.date">
        </el-date-picker>
      </label>
      <label class="filter-label">
        <span class="label-content">姓名</span>
        <el-input v-model="filter.name"></el-input>
      </label>
      <label class="filter-label">
        <span class="label-content">手机号码</span>
        <el-input v-model="filter.phone"></el-input>
      </label>
      <label class="filter-label">
        <span class="label-content">订单状态</span>
        <el-select v-model="filter.status" placeholder="">
          <el-option label="全部" value=""></el-option>
          <el-option label="待确认" value="待确认"></el-option>
          <el-option label="待发货" value="待发货"></el-option>
          <el-option label="已发货" value="已发货"></el-option>
          <el-option label="已签收" value="已签收"></el-option>
          <el-option label="已退货" value="已退货"></el-option>
        </el-select>
      </label>
      <label class="filter-label">
        <span class="label-content">快递公司</span>
        <el-select v-model="filter.express" placeholder="">
          <el-option label="全部" value=""></el-option>
          <el-option label="德邦物流" value="德邦物流"></el-option>
          <el-option label="韵达快递" value="韵达快递"></el-option>
        </el-select>
      </label>
      <div class="mt-m">
        <el-button size="small" type="primary" @click="loadData" :loading="!isLoaded">筛选</el-button>
        <el-button size="small" @click="clearFilter">清空</el-button>
        <span class="remark">共{{ pagination.total }}个订单</span>
      </div>
    </div>
    <el-table stripe :data="orders">
      <el-table-column fixed :width="160" prop="订单ID" label="订单ID" />
      <el-table-column prop="订单状态" label="订单状态" />
      <el-table-column :width="400" prop="套餐明细" label="套餐明细" />
      <el-table-column prop="姓名" label="姓名" />
      <el-table-column :width="120" prop="手机号码" label="手机号码" />
      <el-table-column prop="总价" label="总价" />
      <el-table-column :width="160" prop="下单时间" label="下单时间" />
      <el-table-column prop="省" label="省" />
      <el-table-column prop="市" label="市" />
      <el-table-column prop="快递公司" label="快递公司" />
      <el-table-column :width="140" prop="快递单号" label="快递单号" />
      <el-table-column prop="投放账户" label="投放账户" />
      <el-table-column class="operates" fixed="right" label="操作" width="180">
        <template slot-scope="scope">
          <el-tooltip class="item" effect="dark" content="查看详情" placement="top">
            <el-button size="mini" icon="el-icon-view" type="primary" @click="showOrder(scope.row)"></el-button>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="标记订单" placement="top" v-if="scope.row['快递单号']">
            <el-button size="mini" icon="el-icon-star-off" type="warning" @click="markOrder(scope.row)"></el-button>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="删除记录" placement="top">
            <el-button size="mini" icon="el-icon-delete" type="danger" @click="deleteOrder(scope.row)"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      class="pagination"
      @size-change="changeSize"
      @current-change="changePage"
      :current-page="pagination.page"
      :page-sizes="[20, 50, 100]"
      :page-size="pagination.limit"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pagination.total">
    </el-pagination>
    <el-dialog
      title="订单详情"
      :visible.sync="showDetail"
      width="540px">
      <el-table :height="400" :data="orderDetail">
        <el-table-column width="120" property="key" label="属性"></el-table-column>
        <el-table-column property="value" label="内容"></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="showDetail = false">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="标记订单"
      :visible.sync="showMark"
      width="600px">
      <div class="pt-m pb-m">
        <el-switch
          :disabled="!isMarked"
          v-model="markType.trouble"
          @change="changeMark($event, 'trouble')"
          active-text="问题件">
        </el-switch>
        <span class="ml-l remark">{{ markType.trouble ? '已经' : '尚未' }}收到问题反馈</span>
      </div>
      <div class="pt-m pb-m">
        <el-switch
          :disabled="!isMarked"
          v-model="markType.return"
          active-text="退件">
        </el-switch>
        <span class="ml-l remark">{{ markType.return ? '已经' : '尚未' }}收到退件</span>
      </div>
      <div class="pt-m pb-m">
        <el-switch
          :disabled="!isMarked"
          v-model="markType.profit"
          active-text="返款件">
        </el-switch>
        <span class="ml-l remark">{{ markType.profit ? '已经' : '尚未' }}收到返款</span>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="showMark = false">确 定</el-button>
      </span>
    </el-dialog>
  </section>
</template>

<script>
import XLSX from 'xlsx';

export default {
  name: 'Index',
  data() {
    return {
      filter: {
        id: '',
        name: '',
        phone: '',
        date: '',
        status: '',
        express: '',
      },
      pagination: {
        page: 1,
        limit: 20,
        total: 0,
      },
      file: null,
      orders: [],
      isMarked: false,
      markType: {
        trouble: false,
        return: false,
        profit: false,
      },
      markTarget: null,
      showMark: false,
      showDetail: false,
      orderDetail: null,
      isLoaded: true,
      isImported: true,
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    clearFilter() {
      this.filter.id = '';
      this.filter.name = '';
      this.filter.phone = '';
      this.filter.date = '';
      this.filter.status = '';
      this.filter.express = '';
      this.loadData();
    },
    changeSize(limit) {
      this.pagination.limit = limit;
      this.loadData();
    },
    changePage(page) {
      this.pagination.page = page;
      this.loadData();
    },
    loadData() {
      const { id, name, phone, date, status, express } = this.filter;
      const { limit, page } = this.pagination;
      this.isLoaded = false;
      this.$http.get('/api/orders', {
        params: { limit, page, id, name, phone, status, express, start: date && date[0], end: date && date[1] },
      }).then(({ data }) => {
        this.isLoaded = true;
        this.pagination.total = data.total;
        this.orders = data.orders;
      });
    },
    chooseFile(e) {
      this.file = e.target.files ? e.target.files[0] : null;
    },
    uploadFile() {
      const fileReader = new FileReader();
      this.isImported = false;
      fileReader.onload = (e) => {
        const workbook = XLSX.read(e.target.result, { type: 'binary', cellDates: true, cellStyles: true });
        const orders = XLSX.utils.sheet_to_json(workbook.Sheets[workbook.SheetNames[0]]);
        this.$http.post('/api/orders/upload', { orders }).then(() => {
          this.isImported = true;
          this.file = null;
          this.$refs.upload.value = null;
          this.clearFilter();
          this.$notify({
            title: '导入成功',
            message: '订单导入成功，已自动刷新列表',
            type: 'success',
          });
        }).catch(() => {
          this.$notify({
            title: '导入失败',
            message: '订单导入失败，请重试',
            type: 'error',
          });
        });
      };
      fileReader.readAsBinaryString(this.file);
    },
    markOrder(order) {
      const { _id } = order;
      this.isMarked = false;
      this.markTarget = order;
      this.$http.get(`/api/orders/${_id}/status`).then(({ data }) => {
        this.isMarked = true;
        this.showMark = true;
        this.markType = {
          trouble: data.trouble,
          return: data.return,
          profit: data.profit,
        };
      });
    },
    changeMark(isActive, type) {
      const { _id } = this.markTarget;
      this.isMarked = false;
      this.$http.post(`/api/orders/${_id}/${type}`, {
        isActive,
      }).then(() => {
        this.isMarked = true;
      });
    },
    showOrder(order) {
      this.showDetail = true;
      this.orderDetail = Object.keys(order).map(key => ({ key, value: order[key] }));
    },
    deleteOrder(order) {
      this.$confirm('确定将此订单删除吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        const { _id } = order;
        this.$http.delete(`/api/orders/${_id}`).then(() => {
          this.loadData();
          this.$notify({
            title: '删除成功',
            message: '订单删除成功，已自动刷新列表',
            type: 'success',
          });
        }).catch(() => {
          this.$notify({
            title: '删除失败',
            message: '订单删除失败，请重试',
            type: 'error',
          });
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
        });
      });
    },
  },
};
</script>
